import React, { Component } from 'react';
import Button from '../../../components/UI/Button/Button';
import classes from './ContactData.module.css';
import Spinner from '../../../components/UI/Spinner/Spinner';
import axios from '../../../axios-orders';
import Input from '../../../components/UI/Input/Input';

class ContactData extends Component{
    state = {
        orderForm : {
            name : {
                elementType : 'input',
                elementConfig : {
                    type : 'text',
                    placeholder : 'Your Name'
                },
                value : '',
                validation : {
                    required : true
                },
                valid : false
            },
            email : {
                elementType : 'input',
                elementConfig : {
                    type : 'email',
                    placeholder : 'Your E-mail'
                    },
                    value : '',
                    validation : {
                        required : true
                    },
                    valid : false
                },
            street : {
                elementType : 'input',
                elementConfig : {
                    type : 'text',
                    placeholder : 'Street'
                    },
                    value : '',
                    validation : {
                        required : true
                    },
                    valid : false
                },
            zipcode : {
                elementType : 'input',
                elementConfig : {
                    type : 'text',
                    placeholder : 'ZIP code'
                    },
                    value : '',
                    validation : {
                        required : true
                    },
                    valid : false
                },
            country : {
                elementType : 'input',
                elementConfig : {
                    type : 'text',
                    placeholder : 'Country'
                    },
                    value : '',
                    validation : {
                        required : true
                    },
                    valid : false
                },
            deliveryMethod : {
                elementType : 'select',
                elementConfig : {
                        options : [
                            {value : 'fastest', displayValue : 'Fastest'},
                            {value : 'cheapest', displayValue : 'Cheapest'}
                        ]
                    },
                    value : 'cheapest',
                    validation : {},
                    valid : true
                    }
        },
        loading : false
    }
    orderHandler = (event) => {
        event.preventDefault();
        this.setState({loading : true});
        const formData = {};
        for (let formIdentifierElement in this.state.orderForm){
            formData[formIdentifierElement] = this.state.orderForm[formIdentifierElement].value;
        }
        const order = {
            ingredients : this.props.ingredients,
            price : this.props.price,
            orderData : formData
        }
        axios.post('/orders.json',order)
        .then(response => {
            this.setState({loading : false });
            this.props.history.push('/');
        })
        .catch(error => this.setState({ loading : false }));console.log(this.props.ingredients);
    }

    inputChangeHandler = (event, elementIndex) => {
       const updatedOrderForm = {
           ...this.state.orderForm
       }
       const formElement = {
           ...updatedOrderForm[elementIndex]
       }
        formElement.value = event.target.value;
        formElement.valid = this.checkValidity(formElement.value, formElement.validation);
        updatedOrderForm[elementIndex] = formElement;
        console.log(formElement);
        this.setState({orderForm : updatedOrderForm})
    }

    checkValidity(value, rules){
        let isValid = false;
        
        if(rules.required){
            isValid = value.trim() !== '';
        }
        return isValid;
    }
    render(){
        let formElementsArray = [];
        for (let key in this.state.orderForm){
            formElementsArray.push({
                id : key,
                config : this.state.orderForm[key]
            });
        }
        let form = (
            <form onSubmit={this.orderHandler}>
                    {formElementsArray.map( formElement => (
                        <Input key={formElement.id}
                        elementType={formElement.config.elementType} 
                        elementConfig={formElement.config.elementConfig} 
                        value={formElement.config.value}
                        changed={(event) => this.inputChangeHandler(event, formElement.id)} />
                    ))}
                    <Button btnType="Success">ORDER</Button>      
            </form>
        );
        if(this.state.loading){
            form = <Spinner />;
        }
        return(
            <div className={classes.ContactData}>
                <h4>Enter contact details!</h4>
                {form}
            </div>
        );
    }
}

export default ContactData;